.item {
  width: 100%;
  height: 254px;
  padding: 23px 18px;
  border-radius: 14px;
  position: relative;
  margin: auto;
  cursor: pointer;
  transition: 0.2s all;
}

.title {
  color: rgba(255, 255, 255, 0.95);
  margin: 0 0 16px 0;
  font-size: var(--fontsize-big);
  font-weight: 700;
}

.desc {
  color: white;
  font-size: var(--fontsize-normal);
  font-weight: 600;
  white-space: pre-wrap;
}

.detail_button {
  width: 100%;
  height: 32px;
  line-height: 32px;
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fafafa;
  font-weight: 700;
  font-size: var(--fontsize-bit-small);
  text-align: center;
  background-color: rgba(15, 15, 15, 0.8);
  border-radius: 0 0 12px 12px;
}
